import * as React from "react"
import { Slot } from "@radix-ui/react-slot"
import { cva, type VariantProps } from "class-variance-authority"

import { cn } from "@/lib/utils"

const buttonVariants = cva(
  "inline-flex items-center justify-center gap-2 whitespace-nowrap rounded-lg text-sm font-medium ring-offset-background transition-medical focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:pointer-events-none disabled:opacity-50 [&_svg]:pointer-events-none [&_svg]:size-4 [&_svg]:shrink-0",
  {
    variants: {
      variant: {
        default: "bg-primary text-primary-foreground hover:bg-primary/90 shadow-medical",
        destructive:
          "bg-destructive text-destructive-foreground hover:bg-destructive/90 shadow-medical",
        outline:
          "border border-primary/30 bg-card/50 backdrop-blur-sm text-primary hover:bg-primary/10 hover:text-primary shadow-glow",
        secondary:
          "bg-secondary text-secondary-foreground hover:bg-secondary/80 shadow-medical",
        ghost: "hover:bg-primary/10 hover:text-primary",
        link: "text-primary underline-offset-4 hover:underline",
        medical: "bg-gradient-medical text-primary-foreground hover:scale-105 shadow-pharmaceutical transition-all duration-300",
        hero: "bg-gradient-primary text-primary-foreground hover:shadow-pharmaceutical border border-primary/30 backdrop-blur-sm",
        track: "bg-gradient-medical text-secondary-foreground hover:bg-secondary/90 shadow-medical border border-primary/20"
      },
      size: {
        default: "h-10 px-4 py-2",
        sm: "h-9 rounded-md px-3",
        lg: "h-12 rounded-lg px-8",
        icon: "h-10 w-10",
        hero: "h-14 px-8 text-base"
      },
    },
    defaultVariants: {
      variant: "default",
      size: "default",
    },
  }
)

export interface ButtonProps
  extends React.ButtonHTMLAttributes<HTMLButtonElement>,
    VariantProps<typeof buttonVariants> {
  asChild?: boolean
}

const Button = React.forwardRef<HTMLButtonElement, ButtonProps>(
  ({ className, variant, size, asChild = false, ...props }, ref) => {
    const Comp = asChild ? Slot : "button"
    return (
      <Comp
        className={cn(buttonVariants({ variant, size, className }))}
        ref={ref}
        {...props}
      />
    )
  }
)
Button.displayName = "Button"

export { Button, buttonVariants }
